// install
/** require.context()
 * @description: require.contextwebpackAPI
 * @param {} 
 * @param {} Boolean 
 * @param {} 
 * @return {*}
 */
const requireComponent = require.context('./', true, /\.vue$/)
const install = function (Vue) {
  requireComponent.keys().forEach(fileName => {
    // 
    const file = requireComponent(fileName)
    // 
    const componentName = file.default.name
    Vue.component(componentName, file.default || file)
  })
}
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
export default {
  install
}